@import url('https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap');

body {
  font-family: 'Poppins', serif;
  margin: 0;
  overflow-x: hidden;
}

.foo-home {
  height: 100vh;
  width: 100vw;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.wordCarousel {
  //float: left;
  font-size: 40px;
  font-weight: 100;
  color: #000000;
  margin-top: 0;
  padding: 50vh 0 25vh;
  text-align: center;

  div {
    //float: right;
    overflow: hidden;
    position: relative;
    //text-align: center;
    height: 65px;
    padding-top: 10px;
    margin-top: -10px;

    li {
      color: #000000;
      font-weight: 700;
      padding: 0 10px;
      height: 45px;
      margin-bottom: 45px;
      display: block;
    }
  }
}

@media screen and (max-width: 481px) {
  .wordCarousel {
    font-size: 30px;
  }
}
@media (pointer:none), (pointer:coarse) {
  .foo-home{
    background-position-y: -100px;
    background-position-x: -800px;
    background-size: 2000px;
  }
}

.flip3 {
  animation: flip3 5s cubic-bezier(0.23, 1, 0.32, 1.2) infinite;
  padding-left: 0;
}

@keyframes flip3 {
  0% {
    margin-top: -270px;
  }
  5% {
    margin-top: -180px;
  }
  33% {
    margin-top: -180px;
  }
  38% {
    margin-top: -90px;
  }
  66% {
    margin-top: -90px;
  }
  71% {
    margin-top: 0px;
  }
  99.99% {
    margin-top: 0px;
  }
  100% {
    margin-top: -270px;
  }
}

