@import url(https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap);
body{font-family:'Poppins', serif;margin:0;overflow-x:hidden}.foo-home{height:100vh;width:100vw;background-position:center;background-size:cover;background-repeat:no-repeat;background-attachment:fixed}.wordCarousel{font-size:40px;font-weight:100;color:#000000;margin-top:0;padding:50vh 0 25vh;text-align:center}.wordCarousel div{overflow:hidden;position:relative;height:65px;padding-top:10px;margin-top:-10px}.wordCarousel div li{color:#000000;font-weight:700;padding:0 10px;height:45px;margin-bottom:45px;display:block}@media screen and (max-width: 481px){.wordCarousel{font-size:30px}}@media (pointer: none), (pointer: coarse){.foo-home{background-position-y:-100px;background-position-x:-800px;background-size:2000px}}.flip3{-webkit-animation:flip3 5s cubic-bezier(0.23, 1, 0.32, 1.2) infinite;animation:flip3 5s cubic-bezier(0.23, 1, 0.32, 1.2) infinite;padding-left:0}@-webkit-keyframes flip3{0%{margin-top:-270px}5%{margin-top:-180px}33%{margin-top:-180px}38%{margin-top:-90px}66%{margin-top:-90px}71%{margin-top:0px}99.99%{margin-top:0px}100%{margin-top:-270px}}@keyframes flip3{0%{margin-top:-270px}5%{margin-top:-180px}33%{margin-top:-180px}38%{margin-top:-90px}66%{margin-top:-90px}71%{margin-top:0px}99.99%{margin-top:0px}100%{margin-top:-270px}}

.container {
    height: 100vh;
    width: 100vw;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    color: white;
    overflow-y: hidden;
}

.float-img{
    border-radius: 50%;
    margin: auto;
    margin-top: 30%;
    width: 100px;
    height: 100px;
    background-position: center;
    background-size: 100px;
}

.float-img:hover{
    transition: all 1s;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.about-header{
    float: left;
    width: 50%;
    height: 100%;
    background-color: rgba(255, 231, 166, 0.65);
}

.disc-right{
    float: right;
    width: 50%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgba(60, 119, 224, 0.3);
}

.about-title{
    font-size: 50px;
    text-align: center;
}
.right-heading{
    font-size: 35px;
    font-weight: 300;
}
.context-about{
    font-size: 25px;
    font-weight: 200;
    padding-left: 10px;
}

@media screen and (max-width: 481px){
    .about-header{
        float: none;
        width: 100%;
        height: 15%;
        background-color: rgba(31, 31, 31, 0.7);
    }
    .float-img{
        margin-top: 10px;
        margin-right: 10px;
        float: right;
        width: 80px;
        height: 80px;
        background-position: center top -18px;
    }
    .about-title{
        margin: 0;
        padding-top: 20px;
        font-size: 35px;
    }
    .disc-right{
        float: none;
        height: 80%;
        width: 100%;
        display: inherit;
        -webkit-justify-content: normal;
                justify-content: normal;
        -webkit-align-items: normal;
                align-items: normal;
        background-color: rgba(31, 31, 31, 0.7);

    }
    .right-heading{
        font-size: 30px;
        margin-top: 0;
    }
    .context-about{
        font-size: 23px;
        font-weight: 200;
        padding-left: 6px;
        padding-right: 6px;
        margin: 0;
    }
    .container{
        background-position: top 60px left -300px;
    }
}

.title-skills {
    font-size: 35px;
    text-align: center;
}

.project-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.project{
    border-radius: 2%;
    color: white;
    text-align: center;
    margin: 10px;
    padding: 10px;
    background-size: cover;
    width: 300px;
    height: 400px;
}
.project h1{
    background-color: rgba(47, 47, 47, 0.8);
}
.project h3{
    font-weight: 200;
    background-color: rgba(47, 47, 47, 0.8);
}

.project:hover{
    transition: all .5s;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

a{
    display: block;
    text-decoration: none;
}
.foot_style{
    margin: 0;
    text-align: center;
    float: left;
    width: 25%;
    height: 50px;
    /*background-color: #4285F4;*/
}

.icon-class{
    padding: 10px;
    padding-bottom: 0;
    height: 30px;
    width: 30px;
}

.icon-class:hover{
    transition: all 0.5s;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

@media screen and (max-width: 481px){
    .foot_style{
        width: 100%;
    }
}
